@media (min-width: 580px) {
  .glitch-effect,
  .glitch-effect-white {
    position: relative;
  }
  .glitch-effect-white:after,
  .glitch-effect-white:before,
  .glitch-effect:after,
  .glitch-effect:before {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: #fff;
    clip: rect(0, 0, 0, 0);
  }
  .glitch-effect-white:after,
  .glitch-effect-white:before {
    background: #43656f;
  }
  .glitch-effect-white:after,
  .glitch-effect:after {
    left: 2px;
    text-shadow: -1px 0 #43656f;
    animation: glitch-1 2s infinite linear alternate-reverse;
  }
  .glitch-effect-white:before,
  .glitch-effect:before {
    left: -2px;
    text-shadow: 2px 0 #43656f;
    animation: glitch-2 2s infinite linear alternate-reverse;
  }
  @keyframes glitch-2 {
    0% {
      clip: rect(86px, 920px, 25px, 0);
    }
    5% {
